<template>
  <view>
    <view class="formWrap">
      <view class="blockTitle">填写公司信息</view>
      <view class="formItem">
        <view class="formLabel">公司名称</view>
        <input class="formInput" v-model="companyName" placeholder="请输入公司名称" placeholder-class="plac" />
      </view>
      <view class="formItem">
        <view class="formLabel">公司地址</view>
        <input class="formInput" v-model="companyAddress" placeholder="请输入公司地址" placeholder-class="plac" />
      </view>
    </view>
    <view class="formWrap">
      <view class="blockTitle">请上传名片和工作证件（工牌）招聘</view>
      <view class="tips">1、请确保证件边框完整，图像清晰，以便顺利通过验证！</view>
      <view class="tips">2、信息仅用于身份验证。</view>
      <view class="imgWrap">
        <view class="addPhoneBtn" v-if="!businessCard" @click="chooseImg(1)">
          <image src="/static/images/addPhone.png" mode="widthFix" />
          <view>上传名片</view>
        </view>
        <image class="userImage" v-else :src="businessCard" mode="aspectFill" />
      </view>
      <view class="imgWrap">
        <view class="addPhoneBtn" v-if="!workCard" @click="chooseImg(2)">
          <image src="/static/images/addPhone.png" mode="widthFix" />
          <view>上传工作证</view>
        </view>
        <image class="userImage" v-else :src="workCard" mode="aspectFill" />
      </view>
    </view>
    <view class="confirmBtn" @click="confirm">提交</view>
  </view>
</template>

<script>
export default {
  data () {
    return {
      companyName: '',
      companyAddress: '',
      businessCard: '',
      workCard: ''
    }
  },
  mounted () {
    console.log('component')
  },
  methods: {
    /**
     * 下一步
     */
    confirm () {
      var text = ''
      var flag = false
      if (this.companyName.trim()=='') {
        text = '请输入公司名称'
        flag = true
      } else if (this.companyAddress.trim() == '') {
        text = '请输入公司地址'
        flag = true
      } else if (!this.businessCard) {
        text = '请上传名片'
        flag = true
      } else if (!this.workCard) {
        text = '请上传工作证'
        flag = true
      }
      if(flag) {
        uni.showToast({
          title: text,
          icon: 'none'
        })
        return
      }
      var data = {
        companyName: this.companyName,
        companyAddress: this.companyAddress,
        businessCard: this.businessCard,
        workCard: this.workCard
      }
      this.$emit('confirm', data)
    },
    /**
     * 选择图片 or 拍张
     */
    chooseImg (type) {
      var _this = this
      uni.chooseImage({
        count: 1,
        sourceType: ['album', 'camera'],
        sizeType: ['original', 'compressed'],
        success: res => {
          console.log(res)
          var path = res.tempFilePaths[0]
          _this.$comfun.uploadImg(path).then(res => {
            var data = JSON.parse(res.data)
            if (type == 1) {
              _this.businessCard = data.obj
            } else {
              _this.workCard = data.obj
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
 .formWrap{
    padding: 0 30upx;
    .blockTitle{
      font-size: 34upx;
      font-weight: bold;
      height: 100upx;
      line-height: 100upx;
    }
    .formItem{
      height: 124upx;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1upx solid #EFEFF4;
      .formLabel{
        font-size: 30upx;
      }
      .formInput{
        flex: 1;
        margin-left: 20upx;
        text-align: right;
      }
    }
    .tips{
      font-size: 24upx;
      color: #999;
      line-height: 40upx;
    }
    .imgWrap{
      width: 640upx;
      height: 320upx;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2upx dashed #B9B9B9;
      border-radius: 10upx;
      margin: 36upx auto 0;
      .addPhoneBtn{
        font-size: 28upx;
        color: #999;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        image{
          width: 50upx;
          height: 50upx;
          margin-bottom: 20upx;
        }
      }
      .userImage{
        width: 100%;
        height: 100%;
      }
    }
  }
  .confirmBtn{
    width: 600upx;
    height: 90upx;
    border-radius: 45upx;
    background: linear-gradient(to right, #2F8EC8, #53AADF);
    font-size: 34upx;
    text-align: center;
    line-height: 90upx;
    color: #fff;
    margin: 70upx auto 0;
  }
  .formWrap:first-child{
    margin-bottom: 40upx;
  }
</style>